//hlavn sbor zodpovedn za zobrazovanie zoznamu loh a komunikciu s API

import { useState, useEffect } from "react";
import FilterButtons from "./components/FilterButtons";
import TodoForm from "./components/TodoForm";
import TodoItem from "./components/ToDoItem";
import axios from "axios";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./ToDoList.css";

function TodoList() {
  const [todos, setTodos] = useState([]);
  const [filter, setFilter] = useState("all");
  const [searchTerm, setSearchTerm] = useState("");
  const [displayedTodos, setDisplayedTodos] = useState([]);

  useEffect(() => {
    axios
      .get("https://64061a4440597b65de4a6d35.mockapi.io/todolist")
      .then((response) => {
        setTodos(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  useEffect(() => {
    setDisplayedTodos(filterAndSearchTodos(todos, filter, searchTerm));
  }, [filter, searchTerm, todos]);

  const onSubmit = async (data) => {
    try {
      await TodoForm.schema.validate(data, { abortEarly: false });
      setTodos([...todos, { ...data, completed: false }]);
      TodoForm.reset();
    } catch (errors) {
      errors.inner.forEach((error) => {
        toast.error(error.message);
      });
    }
  };

  const handleComplete = (index) => {
    const newTodos = [...todos];
    newTodos[index].completed = !newTodos[index].completed;
    setTodos(newTodos);
  };

  const handleDelete = (index) => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  };

  const filterAndSearchTodos = (todos, filter, searchTerm) => {
    return todos
      .filter((todo) => {
        if (filter === "all") {
          return true;
        }
        if (filter === "active") {
          return !todo.completed;
        }
        if (filter === "completed") {
          return todo.completed;
        }
      })
      .filter((todo) => {
        return todo.title.toLowerCase().includes(searchTerm.toLowerCase());
      });
  };

  return (
    <>
      <div className="filter-container">
        <FilterButtons
          filter={filter}
          setFilter={setFilter}
          setSearchTerm={setSearchTerm}
          searchTerm={searchTerm}
        />
      </div>
      <TodoForm onSubmit={onSubmit} className="todo-form" />

      <ul className="todo-items-container">
        {displayedTodos.map((todo, index) => (
          <TodoItem
            className="todo-item"
            key={index}
            todo={todo}
            index={index}
            handleComplete={handleComplete}
            handleDelete={handleDelete}
          />
        ))}
      </ul>
    </>
  );
}

export default TodoList;
